import React, { useState } from 'react';
import { url } from './config/config';
import './App.css';
import { useEffect } from 'react';

function App() {
  const [isInput, setInput] = useState(false);
  const [country, setCountry] = useState('');
  const [selectedBrand, setBrand] = useState('');
  const [brandFilter, setBrandFilter] = useState([]);
  const [brands, setBrandsIndex] = useState([])

  useEffect(() => {
    if (brands.length <= 0) {
      fetch(url.baseUrl + url.brandsArray).then(
        res => res.json()
      ).then((data) => {
        setBrandsIndex(data);
      })
    }
  });

  return (
    <div className='container-fluid'>
      <div className='row searchbar'>
        <div className="col-sm-12 col-md-6">
          <div className="row">
            <span className="float-right">कौन सा देश बनाता है?</span>
          </div>
          <div className='row'>
            <span className="float-right">Which country makes?</span>
          </div>
        </div>
        <div className="col-sm-12 col-md-6">
          <input type="text" placeholder='खोजें/Search' className="search_input" value={selectedBrand.brand} name="search" onChange={findProduct} autoComplete="off" autoFocus="" />
        </div>
      </div>
      {isInput ? <div>
        <ul>
          {
            brandFilter.length > 0 ? <div>
              {brandFilter.map((brand, i) => {
                return (
                  <li key={i}>
                    <button value={brand} onClick={(e) => selectBrand(e)}>
                      {brand}
                    </button>
                  </li>
                )
              }
              )}
            </div> :
              <li className='notListed'>Not Listed</li>
          }
        </ul>
      </div> : ''}

      
      <div className={`row main-content ${country ? 'active' : 'inactive'}`}>
            
            <div className="col-sm-12 col-md-3 offset-md-2">
              <div className="card">
                {
                  country === 'india' ?
                    <img className="card-img-top" src="./india-flag.svg" alt="Made In India" />
                    :
                    (country === 'china' ?
                      <img className="card-img-top" src="./china-flag.svg" alt="Made In India" />
                      :
                      <img className="card-img-top" src="./somalia-flag.svg" alt="Made In India" />
                    )
                }
                <div className="card-body">
                  <p className="card-text">{`${country === 'india' ? 'भारत' : (country === 'china' ? 'चीन' : 'अन्य देश')} में निर्मित`}</p>
                  <p className="card-text">{`Made in ${country}`}</p>
                </div>
              </div>
            </div>
            <div className="col-sm-12 col-md-5">
              <div className="card">
                <div className="card-body">
                  <p className="card-text"><b>श्रेणी/Category</b></p>
                  <p className="card-text">{`${selectedBrand.groups}`}</p>
                  <p className="card-text"><b>संदर्भ/References</b></p>
                  <p className="card-text">{`${selectedBrand.references}`}</p>
                </div>
              </div>
            </div>

      </div>

        <div className="container">
          <div className="btn-group-vertical" role="group" aria-label="Button group with nested dropdown">
            <button type="button" className="btn btn-secondary">
              <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSd4NnHXtvv7DpY93-6ZcwUk1tkvL78eT6tSyDNDdT3lY2j0hw/viewform" target="_blank" rel="noopener noreferrer">किसी भी गलत जानकारी की सूचना दें (Report any incorrect data)</a>
            </button>
            <button type="button" className="btn btn-secondary">
              <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfOLjHq0Crnm_6izV9N2WNO9NP9DwYttsZKg9Iqa3j-a5LYOA/viewform" target="_blank" rel="noopener noreferrer">कंपनी / उत्पाद जोड़ने का अनुरोध (Company/Product add request)</a>
            </button>
            <button type="button" className="btn btn-secondary">
              <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfckly2X5CcO-1cEKMBVWDJtJZvjrPW3ivaBb3zvHCCm3bbmg/viewform" target="_blank" rel="noopener noreferrer">सुझाव या शिकायत दर्ज करें (Suggestions or Complaints)</a>
            </button>
          </div>
        </div>

    </div>
  );

  function selectBrand(e) {
    fetch(url.baseUrl + "data-store/brand-" + e.target.value.toLowerCase() + ".json").then(
      res => res.json()
    ).then((data) => {
      console.log(data);
      if (data.alias !== "") {
        fetch(url.baseUrl + "data-store/brand-" + data.alias.toLowerCase() + ".json").then(
          res => res.json()
        ).then(
          data => {
            setBrand(data);
            setCountry(data.country);
          }
        )
      } else {
        console.log(data);
        setBrand(data);
        setCountry(data.country);
      }
    })
    setInput(false);
  }

  function findProduct(e) {
    setBrand(e.target.value);
    let brandFilter = [];
    if (e.target.value) {
      setInput(true);
      setCountry('');
    } else { setInput(false) }
    let text = e.target.value.toLowerCase();
    for (let brand of brands) {
      if (text === brand.substr(0, text.length)) {
        brandFilter.push(brand);
      }
    }
    setBrandFilter(brandFilter);
  }
}

export default App;
